<template>
  <div>

    <form @submit.prevent="agregarPersona(persona)" v-if="agregar">
      <h3 class="text-center">Agregar una Persona</h3>
      <input
        type="text"
        placeholder="Ingrese un Nombre"
        class="form-control my-2"
        v-model="persona.nombre"
      />
      <input
        type="text"
        placeholder="Ingrese una descripcion"
        class="form-control my-2"
        v-model="persona.descripcion"
      />
      <b-button class="btn-sm btn-block btn-success" type="submit">Agregar</b-button>
    </form>

    <form @submit.prevent="editarPersona(personaEdit)" v-else>
      <h3 class="text-center">Editar Persona</h3>
      <input
        type="text"
        placeholder="Ingrese un Nombre"
        class="form-control my-2"
        v-model="personaEdit.nombre"
      />
      <input
        type="text"
        placeholder="Ingrese una descripcion"
        class="form-control my-2"
        v-model="personaEdit.descripcion"
      />
      <b-button class="btn-sm btn-block mb-1 btn-warning" type="submit">Editar</b-button>
      <b-button class="btn-sm btn-block" @click="agregar = true">Cancelar</b-button>
    </form>

    <b-alert
      :show="dismissCountDown"
      dismissible
      :variant="mensaje.color"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged"
    >{{mensaje.texto}}</b-alert>

    <table class="table">
      <thead>
        <tr>
          <th scope="col">#</th>
          <th scope="col">Nombre</th>
          <th scope="col">Descripción</th>
          <th scope="col">Fecha</th>
          <th scope="col">Acciones</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in personas" :key="index">
          <th scope="row">{{item._id}}</th>
          <td>{{item.nombre}}</td>
          <td>{{item.descripcion}}</td>
          <td>{{item.date}}</td>
          <td>
            <b-button class="btn-warning btn-sm mx-2" @click="activarEdicion(item._id)">Actualizar</b-button>
            <b-button class="btn-danger btn-sm mx-2" @click="eliminarPersona(item._id)">Eliminar</b-button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      personas: [],
      persona: {},
      personaEdit: {},
      agregar: true,

      mensaje: { color: "", texto: "" },
      dismissSecs: 5,
      dismissCountDown: 0
    };
  },
  created() {
    this.lista();
  },
  methods: {
    lista() {
      this.axios
        .get("/personas")
        .then(response => {
          this.personas = response.data;
        })
        .catch(e => {
          console.log("error" + e);
        });
    },
    mostrarAlerta() {
      this.mensaje.color = "success";
      this.mensaje.texto = "Agregado";
      this.showAlert();
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    },
    agregarPersona(item) {
      this.axios
        .post("/new-persona", item)
        .then(res => {
          // Agrega al inicio de nuestro arreglo
          this.personas.push(res.data);

          // mensaje de exito
          this.showAlert();
          this.mensaje.texto = "Agregada";
          this.mensaje.color = "success";
        })
        .catch(e => {
          console.log(e.response.data.error.errors.nombre.message);

          // mensaje al fallar
          this.showAlert();
          this.mensaje.color = "danger";
          this.mensaje.texto = e.response.data.error.errors.nombre.message;
        });
      this.persona = {};
    },
    eliminarPersona(id) {
      this.axios
        .delete(`/persona/${id}`)
        .then(res => {
          let index = this.personas.findIndex(
            item => item._id === res.data._id
          );
          this.personas.splice(index, 1);

          this.showAlert();
          this.mensaje.texto = "personas Eliminada";
          this.mensaje.color = "danger";
        })
        .catch(e => {
          console.log(e.response);
        });
    },
    activarEdicion(id) {
      this.agregar = false;
      this.axios
        .get(`/persona/${id}`)
        .then(res => {
          this.personaEdit = res.data;
        })
        .catch(e => {
          console.log(e.response);
        });
    },
    editarPersona(item) {
      this.axios
        .put(`/persona/${item._id}`, item)
        .then(res => {
          let index = this.personas.findIndex(
            item => item._id === this.personaEdit._id
          );
          this.personas[index].nombre = this.personaEdit.nombre;
          this.personas[index].descripcion = this.personaEdit.descripcion;
          this.personaEdit = {};

          this.showAlert();
          this.mensaje.texto = "Persona Actualizada";
          this.mensaje.color = "success";
        })
        .catch(e => {
          console.log(e);
        });
      this.agregar = true;
    }
  }
};
</script>